.project-banner {
  padding: 20px 40px;
  margin: 20px;
  background: #0004;
  border-radius: 10px;
  height: 250px;
  box-sizing: border-box;
  display: grid;
  grid-template-rows: 1fr 1fr;
  position: relative;
  z-index: 2;
  overflow: hidden; }
  .project-banner img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1; }
  .project-banner h1 {
    font-weight: 300; }
  .project-banner p {
    margin-top: 30px; }
  .project-banner .tags {
    align-self: flex-end;
    text-align: right; }
    .project-banner .tags > span {
      font-size: 0.8em;
      padding: 5px;
      border: 1px solid white;
      border-radius: 4px;
      margin: 5px;
      opacity: 0.5;
      transition: 0.25s; }
      .project-banner .tags > span:hover {
        opacity: 1;
        cursor: pointer; }
